<template>
  <div class="success">
    <SuccessHead @copy="copy" />
    <SuccessStep @copy="copy" />
  </div>
</template>
<script>
import SuccessHead from './components/SuccessHead'
import SuccessStep from './components/SuccessStep'
import { Dialog } from 'vant'
import { clickLog } from '@/helper/clickLog'
import { copyText } from '@/utils/index.js'
export default {
  components: { SuccessHead, SuccessStep },
  data () {
    return {
      orderNum: ''
    }
  },
  created () {
    this.orderNum = localStorage.getItem('giftBoxOrderNum')
  },
  methods: {
    async copy () {
      try {
        copyText('微淼精品课')
        await this.$apis.userOperator.getQuickCopy({
          order_num: this.orderNum || '没拿到orderNum'
        })
        window.location.href = 'weixin://'
      } catch (err) {
        const { msg } = err
        Dialog({ message: msg })
      } finally {
        clickLog({
          module_ori: '一键复制',
          module_part: 'xby_lhtf_copy_click'
        })
      }
    }
  }
}
</script>
